export const perPagePrincipalSelector = (state) => state.commercePagination?.principal.perPage || 0;
export const perPageRecommendationSelector = (state, slotId) => state.commercePagination?.recommendations[slotId]?.perPage || 0;
export const totalEntriesPrincipalSelector = (state) => state.commercePagination?.principal.totalEntries || 0;
export const totalEntriesRecommendationSelector = (state, slotId) => state.commercePagination?.recommendations[slotId]?.totalEntries || 0;
export const pagePrincipalSelector = (state) => state.commercePagination?.principal.page || 0;
export const pageRecommendationSelector = (state, slotId) => state.commercePagination?.recommendations[slotId]?.page || 0;
