export const streamAnswer = createAsyncThunk('generatedAnswer/streamAnswer', async (params, config) => {
    const state = config.getState();
    const { dispatch, extra, getState } = config;
    const { search } = getState();
    const { queryExecuted } = search;
    const { setAbortControllerRef } = params;
    const request = await buildStreamingRequest(state);
    const handleStreamPayload = (payloadType, payload) => {
        switch (payloadType) {
            case 'genqa.headerMessageType': {
                const header = JSON.parse(payload);
                dispatch(setAnswerContentFormat(header.contentFormat));
                break;
            }
            case 'genqa.messageType':
                dispatch(updateMessage(JSON.parse(payload)));
                break;
            case 'genqa.citationsType':
                dispatch(updateCitations(JSON.parse(payload)));
                break;
            case 'genqa.endOfStreamType': {
                const isAnswerGenerated = JSON.parse(payload).answerGenerated;
                const cannotAnswer = queryExecuted.length !== 0 && !isAnswerGenerated;
                dispatch(setCannotAnswer(cannotAnswer));
                dispatch(setIsStreaming(false));
                dispatch(setIsAnswerGenerated(isAnswerGenerated));
                dispatch(logGeneratedAnswerStreamEnd(isAnswerGenerated));
                dispatch(logGeneratedAnswerResponseLinked());
                break;
            }
            default:
                if (state.debug) {
                    extra.logger.warn(`Unknown payloadType: "${payloadType}"`);
                }
        }
    };
    dispatch(setIsLoading(true));
    const currentStreamRequestMatchesOriginalStreamRequest = (request) => {
        return (request.streamId ===
            config.getState().search.extendedResults.generativeQuestionAnsweringId);
    };
    const abortController = extra.streamingClient?.streamGeneratedAnswer(request, {
        write: (data) => {
            if (currentStreamRequestMatchesOriginalStreamRequest(request)) {
                dispatch(setIsLoading(false));
                if (data.payload && data.payloadType) {
                    handleStreamPayload(data.payloadType, data.payload);
                }
            }
        },
        abort: (error) => {
            if (currentStreamRequestMatchesOriginalStreamRequest(request)) {
                dispatch(updateError(error));
            }
        },
        close: () => {
            if (currentStreamRequestMatchesOriginalStreamRequest(request)) {
                dispatch(setIsStreaming(false));
            }
        },
        resetAnswer: () => {
            if (currentStreamRequestMatchesOriginalStreamRequest(request)) {
                dispatch(resetAnswer());
            }
        },
    });
    if (abortController) {
        setAbortControllerRef(abortController);
    }
    else {
        dispatch(setIsLoading(false));
    }
});
/**
 * Thunk to handle the sequence of actions required to generate a new answer
 * after a search request.
 *
 *  This action only works when an **answer configuration ID** is present
 * in the engine configuration. In that case, the **Answer API** will be used
 * instead of the regular search pipeline.
 *
 * Flow:
 * 1. Reset the current generated answer state.
 * 2. Construct the Answer API query parameters based on the current state.
 * 3. Fetch a new answer from the Answer API using the provided configuration.
 */
export const generateAnswer = createAsyncThunk('generatedAnswer/generateAnswer', async (_, { getState, dispatch, extra: { navigatorContext, logger } }) => {
    dispatch(resetAnswer());
    const state = getState();
    if (state.generatedAnswer.answerConfigurationId) {
        const answerApiQueryParams = constructAnswerAPIQueryParams(state, navigatorContext);
        // TODO: SVCC-5178 Refactor multiple sequential dispatches into single action
        dispatch(setAnswerApiQueryParams(answerApiQueryParams));
        await dispatch(fetchAnswer(answerApiQueryParams));
    }
    else {
        logger.warn('[WARNING] Missing answerConfigurationId in engine configuration. ' +
            'The generateAnswer action requires an answer configuration ID to use CRGA with the Answer API.');
    }
});
/**
 * Thunk to generate the head answer as part of the generated answer with follow-ups feature.
 *
 * This action initiates the generation of the main answer when using the Answer Generation API
 * with an agent configuration. It requires an **agent ID** to be present in the engine configuration.
 *
 * @internal
 */
export const generateHeadAnswer = createAsyncThunk('generatedAnswerWithFollowUps/generateHeadAnswer', async (_, { getState, dispatch, extra: { navigatorContext, logger } }) => {
    const state = getState();
    const agentId = selectAgentId(state);
    if (!agentId) {
        logger.warn('Missing agentId in engine configuration. ' +
            'The generateHeadAnswer action requires an agent ID.');
        return;
    }
    dispatch(resetAnswer());
    const generateHeadAnswerParams = constructGenerateHeadAnswerParams(state, navigatorContext);
    const headAnswerEndpointArgs = {
        ...generateHeadAnswerParams,
        strategyKey: 'head-answer',
    };
    dispatch(setAnswerApiQueryParams(generateHeadAnswerParams));
    await dispatch(initiateAnswerEndpoint(headAnswerEndpointArgs));
});
