import { logClickEvent, logCustomEvent, logSearchEvent, } from './analytics-actions.js';
/**
 * Returns possible generic analytics action creators.
 *
 * @param engine - The headless engine.
 * @returns An object holding the action creators.
 *
 * @group Actions
 * @category GenericAnalytics
 */
export function loadGenericAnalyticsActions(engine) {
    engine.addReducers({});
    return {
        logSearchEvent,
        logClickEvent,
        logCustomEvent,
    };
}
