import { documentIdentifier, makeAnalyticsAction, partialDocumentInformation, validateResultPayload, } from '../analytics/analytics-utils.js';
export const logShowMoreFoldedResults = (result) => makeAnalyticsAction('analytics/folding/showMore', (client, state) => {
    validateResultPayload(result);
    return client.makeShowMoreFoldedResults(partialDocumentInformation(result, state), documentIdentifier(result));
});
const logShowLessFoldedResults = () => makeAnalyticsAction('analytics/folding/showLess', (client) => {
    return client.makeShowLessFoldedResults();
});
export const foldedResultAnalyticsClient = {
    logShowMoreFoldedResults,
    logShowLessFoldedResults,
};
