import { searchReducer as search } from '../../features/search/search-slice.js';
import { executeSearch, fetchFacetValues, fetchInstantResults, fetchMoreResults, fetchPage, } from './legacy/search-actions.js';
/**
 * Loads the `search` reducer and returns possible action creators.
 *
 * @param engine - The headless engine.
 * @returns An object holding the action creators.
 *
 * @group Actions
 * @category Search
 */
export function loadSearchActions(engine) {
    engine.addReducers({ search });
    return {
        executeSearch,
        fetchMoreResults,
        fetchFacetValues,
        fetchPage,
        fetchInstantResults,
    };
}
